@import "../../config/styles/helpers/mixins";

.app-chuck-joke {
  max-width: 500px;
  margin: 70px auto 0;

  &__approves {
    width: 100%;
    margin-bottom: -5px;
  }

  &__quote {
    background-color: var(--white);
    padding: 20px;
    margin-bottom: 20px;
    text-align: center;
    line-height: 1.5;
    font-size: 18px;
  }
}
